.project_item {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 550px;

	&.reverse {
		flex-direction: row-reverse;
	}

	.project_image {
		border-radius: 50%;

		img {
			clip-path: inset(100% 0% 0% 0%);
			transition: clip-path 800ms cubic-bezier(0.77, 0, 0.175, 1);
			border-radius: 5%;
			width: 100%;

			&.is-inview {
				clip-path: inset(0% 0% 0% 0%);
			}
		}
	}

	.project_title {
		position: relative;
		padding-bottom: 1vw;
		font-size: 2.8vw;
		font-family: "ChenYuluoyan";
	}

	.project_text {
		font-size: 1.4vw;
		font-family: "Serif";
		line-height: 1.6em;
	}

	.project_textbox {
		width: 32%;
		padding: 0 5vw;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}
}

.portfolio {
	padding: 100px 0 150px;
}

.mll {
	padding-top: 20px;
}
